.offline-status {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  padding: var(--space-sm) var(--space-md);
  color: var(--text-white);
  font-size: 0.9rem;
  font-weight: 500;
  box-shadow: var(--shadow-lg);
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
  border-bottom: 3px solid transparent;
}

.offline-status.offline {
  background: linear-gradient(135deg, rgba(230, 57, 70, 0.95), rgba(230, 57, 70, 0.9));
  border-bottom-color: var(--mumbai-red);
  animation: pulse 2s infinite;
}

.offline-status.online {
  background: linear-gradient(135deg, rgba(42, 157, 143, 0.95), rgba(42, 157, 143, 0.9));
  border-bottom-color: var(--mumbai-green);
}

.offline-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  position: relative;
}

.status-icon {
  font-size: 1.1rem;
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.3));
}

.status-text {
  flex: 1;
  text-align: center;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.close-button {
  background: rgba(255, 255, 255, 0.2);
  border: 1px solid rgba(255, 255, 255, 0.3);
  color: var(--text-white);
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0;
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: all 0.3s ease;
  position: absolute;
  right: 0;
  backdrop-filter: blur(5px);
}

.close-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

/* Adjust main content when offline status is shown */
body.offline-status-shown {
  padding-top: 60px;
}

@keyframes pulse {
  0%, 100% {
    opacity: 0.9;
    transform: translateY(0);
  }
  50% {
    opacity: 1;
    transform: translateY(-1px);
  }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .offline-status {
    padding: var(--space-xs) var(--space-sm);
    font-size: 0.8rem;
  }
  
  .offline-content {
    padding: 0 var(--space-lg);
  }
  
  .status-text {
    font-size: 0.8rem;
  }
  
  .status-icon {
    font-size: 1rem;
  }
  
  .close-button {
    width: 24px;
    height: 24px;
    font-size: 1rem;
  }
}