.home {
  max-width: 100%;
  animation: fadeIn 0.8s ease-out;
}

.hero {
  text-align: center;
  padding: var(--space-xl) 0;
  background: var(--mumbai-sunset);
  color: var(--text-white);
  margin: calc(-1 * var(--space-xl)) calc(-1 * var(--space-lg)) var(--space-xl) calc(-1 * var(--space-lg));
  border-radius: 0 0 var(--radius-xl) var(--radius-xl);
  position: relative;
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url("data:image/svg+xml,%3Csvg width='100' height='100' viewBox='0 0 100 100' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23ffffff' fill-opacity='0.1'%3E%3Cpath d='M50 50c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10zm10 0c0-5.5-4.5-10-10-10s-10 4.5-10 10 4.5 10 10 10 10-4.5 10-10z'/%3E%3C/g%3E%3C/svg%3E") repeat;
  pointer-events: none;
}

.hero::after {
  content: 'üåÜ';
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 3rem;
  opacity: 0.3;
  animation: float 4s ease-in-out infinite;
}

.hero h1 {
  font-size: 3rem;
  margin: 0 0 var(--space-md) 0;
  font-weight: 700;
  position: relative;
  z-index: 1;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  background: linear-gradient(45deg, #FFD23F, #FFFFFF, #FFD23F);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hero-subtitle {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  opacity: 0.95;
  position: relative;
  z-index: 1;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.services {
  margin: var(--space-xl) 0;
  animation: slideUp 0.8s ease-out 0.2s both;
}

.services h2 {
  text-align: center;
  font-size: 2.2rem;
  margin-bottom: var(--space-xl);
  color: var(--text-primary);
  position: relative;
}

.services h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: var(--mumbai-sunset);
  border-radius: 2px;
}

.service-grid {
  display: flex;
  justify-content: center;
  margin-top: var(--space-xl);
}

.service-card {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  padding: var(--space-xl);
  text-decoration: none;
  color: inherit;
  box-shadow: var(--shadow-md);
  transition: all 0.4s ease;
  border: 2px solid transparent;
  max-width: 500px;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--mumbai-sunset);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.service-card.featured {
  border: 2px solid var(--mumbai-orange);
  background: linear-gradient(135deg, #ffffff, #fff8e7);
}

.service-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-xl);
  border-color: var(--mumbai-orange);
}

.service-card:hover::before {
  transform: scaleX(1);
}

.service-icon {
  font-size: 4rem;
  text-align: center;
  margin-bottom: var(--space-md);
  filter: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.1));
  animation: bounce-in 0.8s ease-out 0.4s both;
}

.service-card h3 {
  font-size: 1.5rem;
  margin: 0 0 var(--space-md) 0;
  color: var(--text-primary);
  text-align: center;
  font-weight: 600;
}

.service-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  text-align: center;
  margin: 0 0 var(--space-md) 0;
  font-size: 1.1rem;
}

.service-examples {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--space-xs);
  margin-top: var(--space-md);
}

.example {
  background: var(--mumbai-sunset);
  color: var(--text-white);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-xl);
  font-size: 0.9rem;
  font-weight: 500;
  box-shadow: var(--shadow-sm);
  transition: all 0.3s ease;
}

.example:hover {
  transform: scale(1.05);
  box-shadow: var(--shadow-md);
}

.features {
  margin: var(--space-xl) 0;
  padding: var(--space-xl) 0;
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
  animation: slideUp 0.8s ease-out 0.4s both;
}

.features::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--mumbai-pattern);
  opacity: 0.5;
  pointer-events: none;
}

.features h2 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: var(--space-xl);
  color: var(--text-primary);
  position: relative;
  z-index: 1;
}

.feature-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-lg);
  padding: 0 var(--space-lg);
  position: relative;
  z-index: 1;
}

.feature-item {
  text-align: center;
  padding: var(--space-md);
  background: rgba(255, 255, 255, 0.8);
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 107, 53, 0.1);
}

.feature-item:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-md);
  background: rgba(255, 255, 255, 0.95);
}

.feature-item h4 {
  font-size: 1.2rem;
  margin: 0 0 var(--space-xs) 0;
  color: var(--text-primary);
  font-weight: 600;
}

.feature-item p {
  color: var(--text-secondary);
  margin: 0;
  line-height: 1.5;
}

.popular-slang {
  margin: var(--space-xl) 0;
  text-align: center;
  animation: slideUp 0.8s ease-out 0.6s both;
}

.popular-slang h2 {
  font-size: 2rem;
  margin-bottom: var(--space-xl);
  color: var(--text-primary);
  position: relative;
}

.popular-slang h2::before {
  content: 'üí¨';
  position: absolute;
  left: -40px;
  top: 0;
  font-size: 1.5rem;
}

.popular-slang h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 4px;
  background: var(--mumbai-sunset);
  border-radius: 2px;
}

.slang-preview {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-md);
  margin: var(--space-xl) 0;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.slang-item {
  background: var(--bg-card);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-sm);
  border-left: 4px solid var(--mumbai-orange);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.slang-item::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  background: var(--mumbai-sunset);
  border-radius: 0 0 0 30px;
  opacity: 0.1;
}

.slang-item:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
  border-left-color: var(--mumbai-red);
}

.slang-item strong {
  color: var(--text-primary);
  font-size: 1.1rem;
  font-weight: 600;
}

.cta-button {
  display: inline-block;
  background: var(--mumbai-sunset);
  color: var(--text-white);
  padding: var(--space-md) var(--space-xl);
  border-radius: var(--radius-xl);
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
  margin-top: var(--space-xl);
  transition: all 0.4s ease;
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
}

.cta-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.cta-button:hover::before {
  left: 100%;
}

.cta-button:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  color: var(--text-white);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .hero {
    padding: var(--space-lg) 0;
    margin: calc(-1 * var(--space-lg)) calc(-1 * var(--space-md)) var(--space-lg) calc(-1 * var(--space-md));
  }
  
  .hero h1 {
    font-size: 2.2rem;
  }
  
  .hero-subtitle {
    font-size: 1rem;
  }
  
  .service-card {
    padding: var(--space-lg);
  }
  
  .features {
    padding: var(--space-lg) 0;
  }
  
  .feature-list {
    grid-template-columns: 1fr;
    gap: var(--space-md);
    padding: 0 var(--space-md);
  }
  
  .slang-preview {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
  
  .popular-slang h2::before {
    left: -30px;
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .hero {
    margin: calc(-1 * var(--space-md)) calc(-1 * var(--space-sm)) var(--space-md) calc(-1 * var(--space-sm));
  }
  
  .hero h1 {
    font-size: 1.8rem;
  }
  
  .service-examples {
    gap: var(--space-xs);
  }
  
  .example {
    font-size: 0.8rem;
    padding: 0.25rem 0.5rem;
  }
}