/* Mumbai-inspired color palette and global styles */
:root {
  /* Mumbai Colors */
  --mumbai-orange: #FF6B35;
  --mumbai-blue: #004E89;
  --mumbai-yellow: #FFD23F;
  --mumbai-red: #E63946;
  --mumbai-green: #2A9D8F;
  --mumbai-navy: #1A365D;
  --mumbai-cream: #FFF8E7;
  --mumbai-gold: #F4A261;
  
  /* Gradients */
  --mumbai-sunset: linear-gradient(135deg, #FF6B35, #F4A261, #FFD23F);
  --mumbai-ocean: linear-gradient(135deg, #004E89, #2A9D8F);
  --mumbai-sky: linear-gradient(135deg, #87CEEB, #FFD23F);
  
  /* Text Colors */
  --text-primary: #1A365D;
  --text-secondary: #4A5568;
  --text-light: #718096;
  --text-white: #FFFFFF;
  
  /* Background Colors */
  --bg-primary: #FFF8E7;
  --bg-secondary: #F7FAFC;
  --bg-card: #FFFFFF;
  
  /* Shadows */
  --shadow-sm: 0 2px 4px rgba(26, 54, 93, 0.1);
  --shadow-md: 0 4px 12px rgba(26, 54, 93, 0.15);
  --shadow-lg: 0 8px 25px rgba(26, 54, 93, 0.2);
  --shadow-xl: 0 12px 35px rgba(26, 54, 93, 0.25);
  
  /* Border Radius */
  --radius-sm: 8px;
  --radius-md: 12px;
  --radius-lg: 16px;
  --radius-xl: 24px;
  
  /* Spacing */
  --space-xs: 0.5rem;
  --space-sm: 1rem;
  --space-md: 1.5rem;
  --space-lg: 2rem;
  --space-xl: 3rem;
  
  /* Typography */
  --font-heading: 'Poppins', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  --font-body: 'Inter', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-body);
  background: var(--bg-primary);
  color: var(--text-primary);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Mumbai-inspired patterns */
.mumbai-pattern {
  background-image: 
    radial-gradient(circle at 25% 25%, rgba(255, 107, 53, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 75% 75%, rgba(42, 157, 143, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 50% 50%, rgba(255, 210, 63, 0.05) 0%, transparent 50%);
}

/* Scrollbar styling */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--mumbai-orange);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--mumbai-red);
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-heading);
  font-weight: 600;
  line-height: 1.2;
  color: var(--text-primary);
}

h1 {
  font-size: 2.5rem;
  font-weight: 700;
}

h2 {
  font-size: 2rem;
  font-weight: 600;
}

h3 {
  font-size: 1.5rem;
}

/* Button styles */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-sm) var(--space-lg);
  border: none;
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-weight: 500;
  font-size: 1rem;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.btn:hover::before {
  left: 100%;
}

.btn-primary {
  background: var(--mumbai-sunset);
  color: var(--text-white);
  box-shadow: var(--shadow-md);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  color: var(--text-white);
}

.btn-secondary {
  background: var(--mumbai-ocean);
  color: var(--text-white);
  box-shadow: var(--shadow-md);
}

.btn-secondary:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  color: var(--text-white);
}

/* Card styles */
.card {
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  padding: var(--space-lg);
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 107, 53, 0.1);
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-xl);
  border-color: rgba(255, 107, 53, 0.2);
}

/* Form styles */
.form-group {
  margin-bottom: var(--space-md);
}

.form-label {
  display: block;
  margin-bottom: var(--space-xs);
  font-weight: 500;
  color: var(--text-primary);
  font-family: var(--font-heading);
}

.form-input {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid rgba(255, 107, 53, 0.2);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 1rem;
  background: var(--bg-card);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.form-input:focus {
  outline: none;
  border-color: var(--mumbai-orange);
  box-shadow: 0 0 0 3px rgba(255, 107, 53, 0.1);
}

.form-input::placeholder {
  color: var(--text-light);
}

/* Loading and error states */
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: var(--space-lg);
  font-size: 1.1rem;
  color: var(--text-secondary);
  background: var(--bg-card);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-sm);
}

.error {
  background: linear-gradient(135deg, #fee, #fdd);
  border: 2px solid var(--mumbai-red);
  color: var(--mumbai-red);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  margin: var(--space-sm) 0;
  font-weight: 500;
}

.success {
  background: linear-gradient(135deg, #efe, #dfd);
  border: 2px solid var(--mumbai-green);
  color: var(--mumbai-green);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  margin: var(--space-sm) 0;
  font-weight: 500;
}

/* Animation classes */
.fade-in {
  animation: fadeIn 0.6s ease-out;
}

.slide-up {
  animation: slideUp 0.6s ease-out;
}

.bounce-in {
  animation: bounceIn 0.8s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Mumbai-specific decorative elements */
.mumbai-accent {
  position: relative;
}

.mumbai-accent::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  background: var(--mumbai-sunset);
  border-radius: 2px;
}

/* Utility classes */
.text-center {
  text-align: center;
}

.mt-1 { margin-top: var(--space-xs); }
.mt-2 { margin-top: var(--space-sm); }
.mt-3 { margin-top: var(--space-md); }
.mb-1 { margin-bottom: var(--space-xs); }
.mb-2 { margin-bottom: var(--space-sm); }
.mb-3 { margin-bottom: var(--space-md); }

/* Responsive design */
@media (max-width: 768px) {
  :root {
    --space-xs: 0.25rem;
    --space-sm: 0.75rem;
    --space-md: 1rem;
    --space-lg: 1.5rem;
    --space-xl: 2rem;
  }
  
  h1 {
    font-size: 2rem;
  }
  
  h2 {
    font-size: 1.75rem;
  }
  
  h3 {
    font-size: 1.25rem;
  }
  
  .btn {
    padding: var(--space-sm) var(--space-md);
    font-size: 0.9rem;
  }
}

.App {
  min-height: 100vh;
  background: var(--bg-primary);
}

/* Mumbai-inspired loading animation */
.mumbai-loader {
  display: inline-block;
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 107, 53, 0.3);
  border-radius: 50%;
  border-top-color: var(--mumbai-orange);
  animation: mumbai-spin 1s ease-in-out infinite;
}

@keyframes mumbai-spin {
  to {
    transform: rotate(360deg);
  }
}

/* Mumbai train animation for loading states */
.mumbai-train {
  position: relative;
  display: inline-block;
}

.mumbai-train::before {
  content: 'üöÇ';
  font-size: 1.5rem;
  animation: train-move 2s linear infinite;
}

@keyframes train-move {
  0% {
    transform: translateX(-20px);
  }
  100% {
    transform: translateX(20px);
  }
}

/* Mumbai skyline decoration */
.mumbai-skyline {
  position: relative;
  overflow: hidden;
}

.mumbai-skyline::after {
  content: 'üè¢üèóÔ∏èüåâüèôÔ∏è';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 1.5rem;
  opacity: 0.1;
  text-align: center;
  pointer-events: none;
}

/* Monsoon effect */
.monsoon-effect {
  position: relative;
}

.monsoon-effect::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    45deg,
    transparent 30%,
    rgba(135, 206, 235, 0.1) 31%,
    rgba(135, 206, 235, 0.1) 32%,
    transparent 33%
  );
  background-size: 10px 10px;
  animation: monsoon 3s linear infinite;
  pointer-events: none;
}

@keyframes monsoon {
  0% {
    transform: translateX(-10px) translateY(-10px);
  }
  100% {
    transform: translateX(10px) translateY(10px);
  }
}

/* Street food stall effect */
.tapri-effect {
  position: relative;
  background: linear-gradient(135deg, var(--mumbai-cream), var(--bg-card));
}

.tapri-effect::after {
  content: '‚òïü´ñü•™';
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 1.2rem;
  opacity: 0.2;
  animation: steam 2s ease-in-out infinite;
}

@keyframes steam {
  0%, 100% {
    transform: translateY(0px);
    opacity: 0.2;
  }
  50% {
    transform: translateY(-5px);
    opacity: 0.4;
  }
}

/* Mumbai traffic light effect */
.traffic-light {
  display: inline-flex;
  gap: 4px;
  align-items: center;
}

.traffic-light::before {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--mumbai-green);
  animation: traffic-blink 2s infinite;
}

@keyframes traffic-blink {
  0%, 50% {
    background: var(--mumbai-green);
  }
  51%, 100% {
    background: var(--mumbai-red);
  }
}

/* Bollywood sparkle effect */
.bollywood-sparkle {
  position: relative;
}

.bollywood-sparkle::before {
  content: '‚ú®';
  position: absolute;
  top: -5px;
  right: -5px;
  font-size: 1rem;
  animation: sparkle 1.5s ease-in-out infinite;
}

@keyframes sparkle {
  0%, 100% {
    opacity: 0;
    transform: scale(0.5);
  }
  50% {
    opacity: 1;
    transform: scale(1);
  }
}

/* Mumbai local train compartment effect */
.local-compartment {
  background: linear-gradient(90deg, 
    var(--bg-card) 0%, 
    rgba(255, 107, 53, 0.05) 50%, 
    var(--bg-card) 100%
  );
  border-left: 4px solid var(--mumbai-orange);
  border-right: 4px solid var(--mumbai-orange);
  position: relative;
}

.local-compartment::before {
  content: 'üö™';
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.3;
}

.local-compartment::after {
  content: 'üö™';
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0.3;
}

/* Additional responsive enhancements */
@media (max-width: 480px) {
  .mumbai-train::before {
    font-size: 1.2rem;
  }
  
  .mumbai-skyline::after {
    font-size: 1.2rem;
  }
  
  .tapri-effect::after {
    font-size: 1rem;
  }
}