.slang-translator {
  max-width: 800px;
  margin: 0 auto;
  animation: fadeIn 0.8s ease-out;
}

.page-header {
  text-align: center;
  margin-bottom: var(--space-xl);
  padding: var(--space-xl);
  background: var(--bg-card);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  position: relative;
  overflow: hidden;
}

.page-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--mumbai-pattern);
  opacity: 0.3;
  pointer-events: none;
}

.page-header::after {
  content: 'üó£Ô∏èüí¨';
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 2rem;
  opacity: 0.2;
  animation: float 3s ease-in-out infinite;
}

.page-header h1 {
  font-size: 2.5rem;
  margin: 0 0 var(--space-sm) 0;
  background: var(--mumbai-sunset);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  position: relative;
  z-index: 1;
}

.page-header p {
  font-size: 1.1rem;
  color: var(--text-secondary);
  margin: 0;
  position: relative;
  z-index: 1;
}

.translator-content {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.translator-form {
  background: var(--bg-card);
  padding: var(--space-xl);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  border: 2px solid rgba(255, 107, 53, 0.1);
  transition: all 0.3s ease;
}

.translator-form:hover {
  border-color: rgba(255, 107, 53, 0.2);
  box-shadow: var(--shadow-lg);
}

.form-group {
  margin-bottom: var(--space-lg);
}

.form-label {
  display: block;
  margin-bottom: var(--space-sm);
  font-weight: 600;
  color: var(--text-primary);
  font-size: 1.1rem;
}

.input-container {
  position: relative;
}

.form-input {
  width: 100%;
  padding: var(--space-md);
  border: 2px solid rgba(255, 107, 53, 0.2);
  border-radius: var(--radius-md);
  font-family: var(--font-body);
  font-size: 1.1rem;
  background: var(--bg-card);
  color: var(--text-primary);
  transition: all 0.3s ease;
  box-shadow: var(--shadow-sm);
}

.form-input:focus {
  outline: none;
  border-color: var(--mumbai-orange);
  box-shadow: 0 0 0 3px rgba(255, 107, 53, 0.1), var(--shadow-md);
  transform: translateY(-2px);
}

.form-input::placeholder {
  color: var(--text-light);
  font-style: italic;
}

.clear-button {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  background: var(--mumbai-red);
  color: var(--text-white);
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  font-size: 16px;
  line-height: 1;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.clear-button:hover {
  background: var(--mumbai-navy);
  transform: translateY(-50%) scale(1.1);
}

.suggestions {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background: linear-gradient(135deg, #fff8e7, #f7fafc);
  border-radius: var(--radius-md);
  border: 1px solid rgba(255, 107, 53, 0.1);
}

.suggestions-label {
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 var(--space-sm) 0;
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.suggestions-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-xs);
}

.suggestion-item {
  background: var(--mumbai-ocean);
  color: var(--text-white);
  border: none;
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-xl);
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-sm);
}

.suggestion-item:hover {
  background: var(--mumbai-navy);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.form-actions {
  display: flex;
  justify-content: center;
  margin-top: var(--space-lg);
}

.btn {
  padding: var(--space-md) var(--space-xl);
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: var(--radius-md);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.btn:disabled:hover {
  transform: none;
  box-shadow: var(--shadow-md);
}

.translation-result {
  background: var(--bg-card);
  padding: var(--space-xl);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  border: 2px solid var(--mumbai-green);
  animation: slideUp 0.6s ease-out;
  position: relative;
  overflow: hidden;
}

.translation-result::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--mumbai-sunset);
}

.translation-result::after {
  content: '‚ú®';
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 1.5rem;
  opacity: 0.3;
  animation: float 2s ease-in-out infinite;
}

.translation-result h3 {
  margin: 0 0 var(--space-lg) 0;
  color: var(--text-primary);
  font-size: 1.5rem;
  text-align: center;
}

.result-card {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.original-phrase,
.translated-phrase,
.confidence {
  padding: var(--space-md);
  border-radius: var(--radius-md);
  border-left: 4px solid var(--mumbai-orange);
}

.original-phrase {
  background: linear-gradient(135deg, #fff8e7, #f7fafc);
}

.translated-phrase {
  background: linear-gradient(135deg, #e6fffa, #f0fff4);
  border-left-color: var(--mumbai-green);
  font-size: 1.1rem;
}

.confidence {
  background: linear-gradient(135deg, #fef5e7, #fff8dc);
  border-left-color: var(--mumbai-gold);
}

.original-phrase strong,
.translated-phrase strong,
.confidence strong {
  color: var(--text-primary);
  font-weight: 600;
}

.alternatives {
  margin-top: var(--space-md);
  padding: var(--space-md);
  background: linear-gradient(135deg, #f8f9fa, #e9ecef);
  border-radius: var(--radius-md);
  border-left: 4px solid var(--mumbai-blue);
}

.alternatives strong {
  color: var(--text-primary);
  font-weight: 600;
}

.alternatives ul {
  margin: var(--space-sm) 0 0 0;
  padding-left: var(--space-lg);
}

.alternatives li {
  color: var(--text-secondary);
  margin-bottom: var(--space-xs);
}

.error {
  background: linear-gradient(135deg, #fee, #fdd);
  border: 2px solid var(--mumbai-red);
  color: var(--mumbai-red);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  margin: var(--space-sm) 0;
  font-weight: 500;
  text-align: center;
  animation: slideUp 0.4s ease-out;
}

.help-section {
  background: var(--bg-card);
  padding: var(--space-xl);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-md);
  text-align: center;
  position: relative;
  overflow: hidden;
}

.help-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--mumbai-pattern);
  opacity: 0.2;
  pointer-events: none;
}

.help-section h3 {
  margin: 0 0 var(--space-lg) 0;
  color: var(--text-primary);
  font-size: 1.5rem;
  position: relative;
  z-index: 1;
}

.help-section h3::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 60px;
  height: 3px;
  background: var(--mumbai-sunset);
  border-radius: 2px;
}

.popular-terms {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--space-sm);
  position: relative;
  z-index: 1;
}

.term-button {
  background: var(--mumbai-sunset);
  color: var(--text-white);
  border: none;
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-xl);
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-sm);
  position: relative;
  overflow: hidden;
}

.term-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
  transition: left 0.5s;
}

.term-button:hover::before {
  left: 100%;
}

.term-button:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-md);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .slang-translator {
    padding: 0 var(--space-sm);
  }
  
  .page-header {
    padding: var(--space-lg);
  }
  
  .page-header h1 {
    font-size: 2rem;
  }
  
  .translator-form {
    padding: var(--space-lg);
  }
  
  .form-input {
    font-size: 1rem;
  }
  
  .suggestions-list {
    justify-content: center;
  }
  
  .result-card {
    gap: var(--space-sm);
  }
  
  .popular-terms {
    gap: var(--space-xs);
  }
  
  .term-button {
    font-size: 0.9rem;
    padding: var(--space-xs) var(--space-sm);
  }
}

@media (max-width: 480px) {
  .page-header h1 {
    font-size: 1.8rem;
  }
  
  .translator-form {
    padding: var(--space-md);
  }
  
  .help-section {
    padding: var(--space-lg);
  }
}